/**
 * Variable (number) used in Key Info to control number of listings displayed on page
 * load and subsequent 'Load more' clicks
 **/

import // listingsLength,
// batchNumber,
'../key-info-box/key-info-paginated';

const className = 'dynamic';

/**
 *  Wait until all DOM content is rendered. Some class names in other patterns are set dynamically
 *  so these must be present before running functions in this pattern.
 */

document.addEventListener(
    'DOMContentLoaded',
    function() {
        let dynamicElementWrapper = document.querySelector('.dynamic');

        // Target element using 'load more' functionality
        function loadMore() {
            let loadMoreWrapper = document.querySelector('.dynamic--load-more'),
                loadMoreButton = loadMoreWrapper.querySelector(
                    '.content-toggle button'
                );

            // Get number of visible listings after first click
            loadMoreButton.addEventListener('click', () => {
                // console.log(listingsVisible)
            });
        }

        // Detect what type of dynamic element is being used
        function dynamicContentType() {
            if (
                dynamicElementWrapper.classList.contains('dynamic--load-more')
            ) {
                loadMore();
            }
        }
        dynamicContentType();
    },
    false
);

export default {
    launchQuery: `.${className}`,
};
